html,
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
body * {
  font-family: 'Gilroy', sans-serif;
  font-weight: 400;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-size: $font-size-Min;

  // @media (min-width: $mobileBig) {
  font-size: $font-size;
  // }
}
.section {
  width: 300px;
  margin: auto;

  // @media (min-width: $mobileBig) {
  width: 600px;
  // }
}

a {
  text-decoration: none;
}

.container {
  margin: auto;
  width: 100%;

  min-width: 600px;
}

h1 {
  text-align: left;
  font-weight: 800;
  font-size: $font-size-h1-Min;

  // @media (min-width: $mobileBig) {
  font-size: $font-size-h1;
  // }
}

h2 {
  text-align: left;
  font-weight: 400;
  color: $color-theme;
  font-size: $font-size-title-Min;

  // @media (min-width: $mobileBig) {
  font-size: $font-size-title;
  // }
}

.header {
  overflow: hidden;
  background-color: #fff;
  // height: 54px;
  height: 108px;
  padding-right: 10px;

  // @media (min-width: $mobileBig) {
  height: 108px;
  padding-right: 20px;
  // }

  .logo {
    float: left;
    text-align: left;
    // margin-top: 5px;
    // width: 48px;
    width: 96px;
    margin-top: 10px;

    // @media (min-width: $mobileBig) {
    width: 96px;
    margin-top: 10px;
    // }

    a {
      display: block;

      img {
        width: 100%;
      }
    }
  }
  .social {
    float: right;
    text-align: right;
    // padding-top: 28px;
    padding-top: 56px;

    // @media (min-width: $mobileBig) {
    padding-top: 56px;
    // }

    a {
      display: inline-block;
      // width: 20px;
      // margin-left: 13px;
      width: 40px;
      margin-left: 26px;

      // @media (min-width: $mobileBig) {
      width: 40px;
      margin-left: 26px;
      // }

      img {
        width: 100%;
      }
    }
  }
}

.hero {
  // background: url('../img/sky.png');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 100%;
  height: 260px;
  overflow: hidden;

  // @media (min-width: $mobileBig) {
  height: 520px;
  background-position: 50% 80%;
  // }

  .text {
    float: left;
    // width: 150px;
    width: 168px;
    padding-top: 24px;
    // padding-left: 18px;
    padding-left: 5px;

    // @media (min-width: $mobileBig) {
    // width: 300px;
    width: 336px;
    padding-top: 48px;
    // padding-left: 36px;
    padding-left: 10px;
    // }

    .title {
      font-weight: 800;
      color: #fff;
      font-size: $font-size-h1-Min;

      // @media (min-width: $mobileBig) {
      font-size: $font-size-h1;
      // }
    }
    .description {
      color: #fff;
      font-size: $font-size-description-Min;

      // @media (min-width: $mobileBig) {
      font-size: $font-size-description;
      // }
    }
  }

  .image {
    // width: 130px;
    width: 117px;
    padding-top: 44px;
    float: right;

    // @media (min-width: $mobileBig) {
    // width: 260px;
    width: 234px;
    padding-top: 88px;
    // }

    img {
      width: 117px;

      // @media (min-width: $mobileBig) {
      width: 234px;
      // }
    }
  }
}

.list {
  background-color: $color-theme-bg-color;
  padding-top: 12px;
  padding-left: 12px;
  // height: 162px;
  height: 232px;
  overflow: hidden;

  // @media (min-width: $mobileBig) {
  padding-top: 24px;
  padding-left: 24px;
  height: 324px;
  // }

  h2 {
    color: $color-theme;
    margin-bottom: 22px;

    // @media (min-width: $mobileBig) {
    margin-bottom: 44px;
    // }
  }
  .columns {
    overflow: hidden;

    .item {
      float: left;
      width: 30%;
      margin: 1%;

      .ico {
        text-align: center;
        height: 32px;
        margin-bottom: 13px;
        padding-right: 20px;

        // @media (min-width: $mobileBig) {
        height: 64px;
        margin-bottom: 26px;
        padding-right: 40px;
        // }

        &.plus {
          img {
            width: 32px;

            // @media (min-width: $mobileBig) {
            width: 64px;
            // }
          }
        }
        &.list {
          padding-top: 3px;

          // @media (min-width: $mobileBig) {
          padding-top: 6px;
          // }

          img {
            width: 26px;
            // @media (min-width: $mobileBig) {
            width: 52px;
            // }
          }
        }
        &.envelop {
          padding-top: 3px;

          // @media (min-width: $mobileBig) {
          width: 6px;
          // }

          img {
            width: 26px;

            // @media (min-width: $mobileBig) {
            width: 52px;
            // }
          }
        }
      }
      .text {
        color: $color-theme;
      }
    }
  }
}

.footer {
  text-align: center;
  background-color: $color-footer-bg-color;
  height: 125px;
  padding-top: 24px;
  overflow: hidden;

  // @media (min-width: $mobileBig) {
  height: 250px;
  padding-top: 48px;
  // }

  h2 {
    color: #fff;
    font-weight: 700;
    text-align: center;
    margin-bottom: 15px;

    // @media (min-width: $mobileBig) {
    margin-bottom: 30px;
    // }
  }
  .app-row {
    overflow: hidden;
    width: 215px;
    text-align: center;
    margin: auto;

    // @media (min-width: $mobileBig) {
    width: 430px;
    // }

    .app-store {
      float: left;
      width: 100px;

      // @media (min-width: $mobileBig) {
      width: 200px;
      // }

      img {
        width: 100%;
      }
    }
    .google-play {
      float: right;
      width: 100px;

      // @media (min-width: $mobileBig) {
      width: 200px;
      // }

      img {
        width: 100%;
      }
    }
  }
}

